html {
  font-family: sans-serif;
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
footer,
header,
nav,
section {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

figcaption,
figure,
main {
  display: block;
}

figure {
  margin: 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
  outline: none;
}

a:active,
a:hover {
  outline-width: 0;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: inherit;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

dfn {
  font-style: italic;
}

mark {
  background-color: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

audio,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

button,
input,
optgroup,
select,
textarea {
  font: inherit;
  margin: 0;
  border-radius: 0;
}

optgroup {
  font-weight: bold;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  display: inline-block;
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details,
menu {
  display: block;
}

summary {
  display: list-item;
}

canvas {
  display: inline-block;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a img {
  border: none;
  outline: none;
}

img {
  display: block;
  max-width: 100%;
}

@font-face {
  font-family: "Roboto-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("../assets/fonts/Roboto-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Roboto-Italic";
  font-style: normal;
  font-weight: 400;
  src: url("../assets/fonts/Roboto-Italic.ttf") format("truetype");
}

html {
  font-size: 10px;
}

@media (max-width: 767px) {
  html {
    font-size: 2.66667vw;
  }
}

@media (min-width: 768px) {
  html {
    font-size: 1.30208vw;
  }
}

@media (min-width: 768px) {
  html {
    font-size: 0.69444vw;
  }
}

@media (min-width: 1440px) {
  html {
    font-size: 10px;
  }
}

body {
  font-family: "Roboto-Regular", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
}

@media (max-width: 767px) {
  body.fixed {
    overflow: hidden;
  }
}

a {
  text-decoration: none;
}

#main {
  width: 100%;
  max-width: 100%;
  margin: 0;
  padding: 0;
}

.l {
  width: 100%;
  padding: 0 5rem;
}

@media (min-width: 768px) {
  .l {
    padding: 0 8rem;
  }
}

@media (min-width: 768px) {
  .l {
    max-width: 1440px;
    margin: auto;
  }
}

picture {
  width: 100%;
}

img {
  width: 100%;
}

h4 {
  font-size: 1.4rem;
  font-weight: 900;
  text-transform: capitalize;
  line-height: 1.14;
  letter-spacing: normal;
  font-family: sans-serif;
  padding-top: 0.9rem;
}

@media (min-width: 768px) {
  h4 {
    padding-top: 3rem;
    font-size: 1.8rem;
    line-height: 1.28;
  }
}

p {
  display: none;
}

.grid-container {
  margin: 0 auto;
  display: grid;
  width: 33.6rem;
  align-items: baseline;
  grid-template-columns: 15.6rem 16rem;
  grid-column-gap: 2rem;
  grid-row-gap: 2rem;
}

.grid-container .ap1 {
  grid-row-start: 1;
  grid-column-start: 1;
}

.grid-container .ap2 {
  grid-row-start: 1;
  grid-column-start: 2;
}

.grid-container .ap3 {
  grid-row-start: 2;
  grid-column: 1 / -1;
}

.grid-container .ap4 {
  grid-row-start: 3;
  grid-column: 1/ -1;
  display: flex;
}

.grid-container .ap4 h4 {
  padding: 0 0.3rem 0 1rem;
}

@media (min-width: 768px) {
  .grid-container {
    width: 110rem;
    grid-template-columns: 20rem 34.4rem 49.2rem;
    grid-template-rows: 13.6rem 45.6rem;
    grid-column-gap: 3.2rem;
    grid-row-gap: 0;
  }
  .grid-container .ap1 {
    grid-row-start: 2;
    grid-column-start: 1;
  }
  .grid-container .ap2 {
    grid-row-start: 2;
    grid-column-start: 2;
  }
  .grid-container .ap3 {
    grid-row-start: 1;
    grid-column-start: 3;
  }
  .grid-container .ap4 {
    grid-row-start: 3;
    grid-column-start: 3;
    margin-top: 1.6rem;
  }
  .grid-container .ap4 h4 {
    padding: 0 1rem 0 3rem;
  }
}

.c-test {
  font-size: 2.5rem;
  font-weight: 700;
  color: #ffffff;
}

.b-test {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5rem;
  background: #74d0e4;
  font-family: "Roboto-Italic", Arial, sans-serif;
}



